/**
 * Login Screen for Research Preview Access
 * iGFAP Stroke Triage Assistant
 */

import { t } from '../../localization/i18n.js';
import { authManager } from '../../auth/authentication.js';
import { store } from '../../state/store.js';

export function renderLoginScreen() {
  return `
    <div class="login-container">
      <div class="login-card">
        <div class="login-header">
          <div class="app-logo">
            <svg class="logo-icon-svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg" aria-label="iGFAP Brain Logo">
              <!-- Brain icon with GFAP molecular structure -->
              <defs>
                <linearGradient id="brainGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                  <stop offset="0%" style="stop-color:#60a5fa;stop-opacity:1" />
                  <stop offset="100%" style="stop-color:#3b82f6;stop-opacity:1" />
                </linearGradient>
              </defs>
              <!-- Brain outline -->
              <path d="M50,15 C30,15 15,30 15,50 C15,55 16,60 18,64 C20,68 25,75 30,80 C35,85 45,90 50,90 C55,90 65,85 70,80 C75,75 80,68 82,64 C84,60 85,55 85,50 C85,30 70,15 50,15 Z"
                    fill="url(#brainGradient)" stroke="white" stroke-width="2"/>
              <!-- Neural pathways (simplified GFAP structure) -->
              <path d="M35,35 L42,45 M48,30 L48,50 M58,35 L65,45 M42,55 L42,70 M58,55 L58,70"
                    stroke="white" stroke-width="2" stroke-linecap="round" opacity="0.8"/>
              <!-- Center nucleus -->
              <circle cx="50" cy="50" r="6" fill="white" opacity="0.9"/>
            </svg>
            <h1>iGFAP Stroke Triage</h1>
            <div class="version-badge">Research Preview v2.1</div>
          </div>
        </div>

        <div class="login-content">
          <div class="access-notice">
            <h2> ${t('researchAccessRequired')}</h2>
            <p>${t('researchPreviewDescription')}</p>

            <div class="research-disclaimer">
              <h3> ${t('importantNotice')}</h3>
              <ul>
                <li><strong>${t('researchUseOnly')}</strong> - ${t('researchUseOnlyDesc')}</li>
                <li><strong>${t('noPatientDataStorage')}</strong> - ${t('noPatientDataStorageDesc')}</li>
                <li><strong>${t('clinicalAdvisory')}</strong> - ${t('clinicalAdvisoryDesc')}</li>
                <li><strong>${t('contact')}:</strong> Deepak Bos (bosdeepak@gmail.com)</li>
              </ul>
            </div>
          </div>

          <form id="loginForm" class="login-form">
            <div class="form-group">
              <label for="researchPassword">${t('researchAccessCode')}</label>
              <input
                type="password"
                id="researchPassword"
                name="password"
                required
                autocomplete="off"
                placeholder="${t('enterResearchAccessCode')}"
                class="password-input"
              >
            </div>

            <div id="loginError" class="error-message" style="display: none;"></div>

            <button type="submit" class="login-button primary">
              <span class="button-text">${t('accessResearchSystem')}</span>
              <span class="loading-spinner" style="display: none;"></span>
            </button>
          </form>

          <div class="login-footer">
            <details class="regulatory-details">
              <summary class="regulatory-summary">
                <span class="regulatory-icon"></span>
                <span>${t('regulatoryInformation') || 'Regulatory Information'}</span>
                <span class="toggle-icon"></span>
              </summary>
              <div class="regulatory-notice">
                <p><strong>${t('regulatoryStatus')}:</strong> ${t('regulatoryStatusDesc')}</p>
                <p><strong>${t('dataProtection')}:</strong> ${t('dataProtectionDesc')}</p>
                <p><strong>${t('clinicalOversight')}:</strong> ${t('clinicalOversightDesc')}</p>
              </div>
            </details>
          </div>
        </div>
      </div>
    </div>
  `;
}

export function initializeLoginScreen() {
  const loginForm = document.getElementById('loginForm');

  if (!loginForm) {
    return;
  }

  const passwordInput = document.getElementById('researchPassword');
  const loginError = document.getElementById('loginError');
  const loginButton = loginForm.querySelector('.login-button');

  // Focus password input on load
  passwordInput.focus();

  loginForm.addEventListener('submit', async (e) => {
    e.preventDefault();

    const password = passwordInput.value.trim();

    if (!password) {
      showLoginError('Please enter the research access code');
      return;
    }

    // Show loading state
    setLoginLoading(true);
    hideLoginError();

    try {
      const authResult = await authManager.authenticate(password);

      if (authResult.success) {
        // Log successful authentication
        store.logEvent('auth_success', {
          timestamp: new Date().toISOString(),
          userAgent: navigator.userAgent.substring(0, 100), // Limited info
        });

        // Navigate to main application
        store.navigate('triage1');
      } else {
        // Handle authentication failure
        const errorMessage = authResult.message;

        showLoginError(errorMessage);
        passwordInput.value = '';
        passwordInput.focus();

        // Log failed attempt (no sensitive data)
        store.logEvent('auth_failed', {
          timestamp: new Date().toISOString(),
          errorCode: authResult.errorCode,
        });
      }
    } catch (error) {
      showLoginError('Authentication system error. Please try again.');
      // Remove // for production
    } finally {
      setLoginLoading(false);
    }
  });

  // Hide error on input
  passwordInput.addEventListener('input', () => {
    hideLoginError();
  });

  function showLoginError(message) {
    loginError.textContent = message;
    loginError.style.display = 'block';
    passwordInput.classList.add('error');
  }

  function hideLoginError() {
    loginError.style.display = 'none';
    passwordInput.classList.remove('error');
  }

  function setLoginLoading(isLoading) {
    const buttonText = loginButton.querySelector('.button-text');
    const loadingSpinner = loginButton.querySelector('.loading-spinner');

    if (isLoading) {
      buttonText.style.display = 'none';
      loadingSpinner.style.display = 'inline';
      loginButton.disabled = true;
    } else {
      buttonText.style.display = 'inline';
      loadingSpinner.style.display = 'none';
      loginButton.disabled = false;
    }
  }
}
